export default {
	async checkImgHttp(imgPath, type) {
		let res = await this.getImageInfo_PromiseFc(imgPath)
		let newPath = '';
		if (!res) {
			uni.hideLoading()
			switch (type) {
				case 'avatar':
					console.log('%cerr:: ' + imgPath, 'color:green;background:yellow');
					break;
				case 'bgImage':
					console.log('%cerr:: ' + imgPath, 'color:green;background:yellow');
					break;
				case 'wxCode':
					console.log('%cerr:: ' + imgPath, 'color:green;background:yellow');
					break;
				case 'goodsImage':
					console.log('%cerr:: ' + imgPath, 'color:green;background:yellow');
					break;
				default:
					break;
			}
		} else {
			let pathArr = imgPath.split('://');





			pathArr[0] = 'https'

			newPath = pathArr.join('://');
		}

		return newPath;
	},
	// 
	getImageInfo_PromiseFc(imgPath) {
		return new Promise((rs, rj) => {
			imgPath = this.checkMPUrl(imgPath);
			uni.getImageInfo({
				src: imgPath,
				success: res => {
					rs(1);
				},
				fail: err => {
					console.log(err)
					rs(0);
				}
			})
		});
	},
	// 
	checkMPUrl(url) {

		if (
			url.substring(0, 4) === 'http' &&
			url.substring(0, 5) !== 'https' &&
			url.substring(0, 12) !== 'http://store' &&
			url.substring(0, 10) !== 'http://tmp' &&
			url.substring(0, 10) !== 'http://usr'
		) {
			url = 'https' + url.substring(4, url.length);
		}

		return url;
	},




	/**
	 * 
	 * @param {Object} bgObj -  
	 * @param {Number} num -  
	 */
	getScale(bgObj, num = 0) {
		return Number(bgObj.width * (num / bgObj.width).toFixed(2))
	},



	/**
	 * 
	 * @param {Object} bgObj -  
	 * @param {Array} drawArr -  
	 */
	initDrawArray(bgObj, drawArr) {
		const that = this;
		let arr = []
		drawArr.forEach(item => {
			switch (item.type) {
				case 'text':
					arr.push({
						type: 'text',
						text: item.text, //
						size: that.getScale(bgObj, item.size) || 50, //
						color: item.color || 'black', //
						alpha: item.alpha || 1, //
						textAlign: item.textAlign || 'left', //x: 'left''middle''right'
						textBaseline: item.textBaseline ||
							'middle', //y: 'top''bottom''middle''normal'
						dx: that.getScale(bgObj, item.dx) || 0, //x
						dy: that.getScale(bgObj, item.dy + item.size) || 0, //y
						[item.lineFeed ? 'lineFeed' : '']: { //
							maxWidth: item.lineFeed?.maxWidth && that.getScale(bgObj, item.lineFeed
									.maxWidth) || bgObj
								.width, //
							lineHeight: item.lineFeed?.lineHeight && that.getScale(bgObj, item
								.lineFeed.lineHeight) || 50, //
							lineNum: item.lineFeed?.lineNum || -1, // 0
							dx: item.lineFeed?.dx || -1, //0
						},
						infoCallBack: (textLength) => { //
							if (item.isBgCenter) {
								return {
									dx: (bgObj.width - textLength) / 2,
								}
							}
							return {}
						},
						[item.lineThrough ? 'lineThrough' : '']: { // 
							width: item.lineThrough?.width || item.size / 10,
							style: item.lineThrough?.style || item.color,
							alpha: item.lineThrough?.alpha || 1,
							cap: item.lineThrough?.cap || 'butt',
						},
						fontStyle: item.fontStyle || 'normal', // 
						fontVariant: item.fontVariant || 'normal',
						fontWeight: item.fontWeight || 'normal',
						fontFamily: item.fontFamily || 'sans-serif',
					})
					break;
				case 'qrcode':
					arr.push({
						type: 'qrcode',
						text: String(item.text || '') || '', // 
						size: Number(that.getScale(bgObj, item.size) || 0) || 200, // 
						background: String(item.background || '') || '#ffffff', // 
						foreground: String(item.foreground || '') || '#000000', // 
						correctLevel: Number(item.correctLevel || 0) || 3, // 
						image: String(item.image || '') || '', // 
						imageSize: Number(that.getScale(bgObj, item.imageSize) || 0) || 40, // 
						dx: item.isBgCenter ? (bgObj.width - item.size) / 2 : Number(that.getScale(
							bgObj, item.dx) || 0) || 0,
						dy: Number(that.getScale(bgObj, item.dy) || 0) || 0, // y

					})
					break;
				case 'image':
					arr.push({
						type: 'image',
						url: this.checkImgHttp(item.url, item.name), // 
						dWidth: Number(that.getScale(bgObj, item.dWidth) || 0) || 100, //
						dHeight: Number(that.getScale(bgObj, item.dHeight) || 0) || 100, //
						sWidth: Number(that.getScale(bgObj, item.sWidth) || 0) || 100, //
						sHeight: Number(that.getScale(bgObj, item.sHeight) || 0) || 100, //
						dx: Number(that.getScale(bgObj, item.dx) || 0) || 0, // x
						dy: Number(that.getScale(bgObj, item.dy) || 0) || 0, // y
						sx: Number(that.getScale(bgObj, item.sx) || 0) || 0,
						sy: Number(that.getScale(bgObj, item.sy) || 0) || 0,
						[item.circleSet ? 'circleSet' : '']: { //
							r: Number(item.circleSet?.r && that.getScale(bgObj, item.circleSet
								.r) || 0),
							x: Number(item.circleSet?.x && that.getScale(bgObj, item.circleSet
								.x) || 0),
							y: Number(item.circleSet?.y && that.getScale(bgObj, item.circleSet
								.y) || 0),
						},
						[item.roundRectSet ? 'roundRectSet' : '']: { //
							r: Number(item.roundRectSet?.r && that.getScale(bgObj, item
								.roundRectSet.r) || 0),
						},
						infoCallBack: (imgInfo) => { //
							if (item.isBgCenter) {
								return {
									dx: (bgObj.width - item.dWidth) / 2,
								}
							}
						},
					})
					break;
				default:
					_app.log('');
					break;
			}
		})
		return arr
	}



}
