/**
 * 
 * @param {String} scopeValue - isMessage,false
 *                         
 */

const TIPS_MAP = {
	'userInfo': '',
	'userLocation': '', //manifestpermission
	'address': '',
	'userLocationBackground': '',
	'record': '',
	'writePhotosAlbum': '',
	'camera': '',
	'invoice': '',
	'invoiceTitle': '',
	'werun': '',
	'message': ''
}

export default class MpAuth {

	constructor(scopeValue) {
		this.scopeValue = scopeValue
		this.isMessage = scopeValue === 'message' ? true : false
	}

	// 
	checkAuth() {
		const that = this;
		return new Promise((resolve, reject) => {
			uni.getSetting({
				withSubscriptions: that.isMessage, //
				success: res => {
					if (!that.isMessage) { // 
						if (!res.authSetting[
								`scope.${this.scopeValue}`]) { //
							uni.authorize({ //successfai
								scope: `scope.${this.scopeValue}`,
								success: res => { //
									console.log(
										`%c${TIPS_MAP[this.scopeValue]}1`,
										'color:green;background:yellow');
									resolve(1)
								},
								fail: err => { //
									console.log(
										`%c${TIPS_MAP[this.scopeValue]}`,
										'color:green;background:yellow');
									resolve(0)
									that.setAuth()
								}
							})
						} else { //
							console.log(`%c${TIPS_MAP[this.scopeValue]}2`,
								'color:green;background:yellow');
							resolve(1)
						}
					} else { // TODO

					}
				},
				fail: err => {
					console.log(`%cuni.getSetting`, 'color:green;background:yellow');
					console.log(err);
				}
			})
		})


	}

	// 
	setAuth() {
		uni.showModal({
			title: '',
			content: `${TIPS_MAP[this.scopeValue]}`,
			confirmText: '',
			success: res => {
				res.confirm && uni.openSetting()
			},
			fail: err => {
				console.log(`%cuni.showModal`, 'color:green;background:yellow');
			}
		})
	}

}
