/**
 * uview 
 * @description 
 */

// mixin
import mixin from './libs/mixin/mixin.js'
// 
import timeFormat from './libs/function/timeFormat.js'
// ,
import timeFrom from './libs/function/timeFrom.js'
// guid
import guid from './libs/function/guid.js'
// 
import deepClone from './libs/function/deepClone.js'
// 
import addUnit from './libs/function/addUnit.js'
// 
import test from './libs/function/test.js'
// 
import $parent from './libs/function/$parent.js'
// 
import trim from './libs/function/trim.js'
// toastuni.showToast
import toast from './libs/function/toast.js'
// sys()os()
// $usys()(system)
// ios"ios""android"$u.os()
import {
	sys,
	os
} from './libs/function/sys.js'
// 
import debounce from './libs/function/debounce.js'
// 
import throttle from './libs/function/throttle.js'
// 
import config from './libs/config/config.js'
// fixedz-index
import zIndex from './libs/config/zIndex.js'

const $u = {
	timeFormat,
	timeFrom,
	guid,
	sys,
	os,
	test,
	deepClone,
	$parent,
	addUnit,
	trim,
	toast,
	config, // uView
	zIndex,
	debounce,
	throttle,
}

// $uuni
uni.$u = $u

const install = Vue => {
	Vue.mixin(mixin)
	// timeFormat
	Vue.filter('timeFormat', (timestamp, format) => {
		return timeFormat(timestamp, format)
	})
	// 
	Vue.filter('timeFrom', (timestamp, format) => {
		return timeFrom(timestamp, format)
	})
	Vue.prototype.$u = $u
}

export default {
	install
}
