/**
 * Validate v1.0.0 
 */
import test from '@/uview-ui/libs/function/test.js'
export default {

	// 
	mobile: [{
			required: true,
			message: '',
			trigger: ['change', 'blur']
		},
		{
			validator: (rule, value, callback) => {
				return test.mobile(value);
			},
			message: '',
			trigger: ['change', 'blur']
		}
	],
	// 
	account: [{
		required: true,
		min: 5,
		message: '',
		trigger: ['change', 'blur']
	}],

	// 
	code: [{
		required: true,
		min: 4,
		message: '',
		trigger: ['change', 'blur']
	}],

	// 
	password: [{
			required: true,
			message: '',
			trigger: ['change', 'blur']
		},
		{
			pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]+\S{5,12}$/,
			message: '6-12',
			trigger: ['change', 'blur']
		}
	],

	// 
	realName: [{
			required: true,
			message: '',
			trigger: ['change', 'blur']
		},
		{
			validator: (rule, value, callback) => {
				return test.chinese(value);
			},
			message: '',
			trigger: ['change', 'blur']
		}
	],

	// 
	bankName: [{
			required: true,
			message: '',
			trigger: ['change', 'blur']
		},
		{
			validator: (rule, value, callback) => {
				return test.chinese(value);
			},
			message: '',
			trigger: ['change', 'blur']
		}
	],

	// 
	bankCode: [{
			required: true,
			message: '',
			trigger: ['change', 'blur']
		},
		{
			validator: (rule, value, callback) => {
				return test.number(value);
			},
			message: '',
			trigger: ['change', 'blur']
		}
	],

	// 
	alipayAccount: [{
			required: true,
			message: '',
			trigger: ['change', 'blur']
		},
		{
			validator: (rule, value, callback) => {
				let isRange = test.rangeLength(value, [6, 30]);
				let isAsterisk = test.contains(value, '*');
				
				return isRange && !isAsterisk;
			},
			message: '',
			trigger: ['change', 'blur']
		}
	],
}
