import Request from './request'
import apiList from './apis.js'
import store from '@/shopro/store/index.js'

const shoproRequest = new Request();
export default function http(
	url,
	data = {},
	toastBefore = '', // 
	toastAfter = true, // 
) {
	let api = getApiPath(url);
	/*  */
	shoproRequest.interceptor.request((config, cancel) => {
		let token = uni.getStorageSync('token');
		if (api.auth && !token) {
			store.dispatch('showAuthModal');
			uni.hideLoading()
			throw (`,API: '${api.url}'`);
		}
		token && shoproRequest.setConfig(config => {
			config.header.token = token
		})
		if (toastBefore !== '') {
			uni.showLoading({
				title: toastBefore,
				mask: true
			});
		}
		return config
	});

	/*  */
	shoproRequest.interceptor.response((response) => {
		uni.hideLoading();
		if (response.code === 0) {
			if (toastAfter) {
				uni.showToast({
					title: response.msg || ',',
					icon: 'none',
					duration: 1000,
					mask: true
				});
			}

		}

		// token
		if (response.code === 401) {
			store.dispatch('logout');
			store.dispatch('showAuthModal');
			throw (`,API: '${api.url}'`);
		}
		return response
	})

	return shoproRequest.request({
		url: api.url,
		data,
		method: api.method
	})

}

// 
function getApiPath(url) {
	let apiArray = url.split(".");
	let api = apiList;
	apiArray.forEach(v => {
		api = api[v];
	});
	return api;
}
